import React, { useEffect, useState } from 'react'
import { doLogin } from '../util/DataUtil';
import '../css/treepage.css'


export default function Login() {

    const [userid, setUserid] = useState()
    const [password, setPassword] = useState()
    const [site, setSite] = useState("apacpresales-sec-cmdm.mdm.stibosystems.com");
    const [msg, setMsg] = useState("")
    const params = new URLSearchParams(window.location.search)

    useEffect(()=>{
        let sessionvalid = params.get("sessionvalid")
        if(sessionvalid === "N"){
            setMsg("Session Expired.")
        }
    },[params])


    function handleLoginClick() {
        localStorage.clear();
        localStorage.setItem('stepurl', site)
        doLogin(userid, password, (result) => {
            console.log(result)
            if (result.success === "N") {
                setMsg("Login Failed: " + result.msg.message)
            } else {
                setMsg("Login Success")
            }
        })

    }



    function handleEnter() {
        if (window.event.keyCode === 13) {
            handleLoginClick();
        }
    }

    const changeSite = (e) => {
        setSite(e.target.value)
        localStorage.setItem('stepurl', e.target.value)
    }

    return (
        <div style={{
            background: "url(https://www.stibosystems.com/hubfs/2020wot/headers/sustainability-master-data-management.webp) center no-repeat",
            backgroundSize: "100%",
            backgroundPositionY: "100px",
            width: "100%",
            display: "flex", justifyContent: "center", alignItems: "center"
        }}>
            <div style={{ width: '45%', height: "700px"}}>
            </div>
            <div style={{ display: "flex", width: '55%', height: "700px", justifyContent: "start", alignItems: "center", marginLeft: "50px", marginTop:"70px" }}>

                <div style={{ display: "block", width: "70%" }}>
                    <h1>STEP LOGIN</h1>
                    <div>
                        <label htmlFor="userid" className="inp">
                            <input type="text" id="userid" placeholder="&nbsp;" value={userid} onChange={(e) => { setUserid(e.target.value) }} style={{ width: '90%' }} />
                            <span className="label">UserId</span>
                            <span className="focus-bg"></span>
                        </label>
                    </div>
                    <div>
                        <label htmlFor="password" className="inp">
                            <input type="password" id="password" placeholder="&nbsp;" value={password} onChange={(e) => { setPassword(e.target.value) }} style={{ width: '90%' }} onKeyDown={handleEnter} />
                            <span className="label">PassWord</span>
                            <span className="focus-bg"></span>
                        </label>
                    </div>
                    <div style={{ marginTop: '26px' }}>
                        <div className="select_input">
                            <select onChange={changeSite} className='select' value={site}>
                                <option value={"apacpresales-sec-cmdm.mdm.stibosystems.com"}>SEC Poc Site</option>
                                <option value={"apacpresales-hmckia-step.mdm.stibosystems.com"}>HMC-KIA Poc Site</option>
                            </select>
                            <div></div>
                        </div>
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}><button className='btn-primary' style={{ width: '90%' }} type='button' onClick={handleLoginClick}>Login</button></div>
                    <div style={{ color: "#ff4e00", fontSize: "25px", fontWeight: "bold", display: "flex", justifyContent: "center" }}>
                        {msg}
                    </div>
                </div>

            </div>


        </div>

    )
}
