
export const Msgarea = ({ msgs,resetMsgs }) => {
    return (
        <>
            {
                msgs.length > 0 ?
                    <>
                        {msgs.map(msg => {
                            return (
                                <div className="msg-bg">
                                    <div className="msgarea">
                                        <div id="msgheader">
                                            {msg.type}
                                        </div>
                                        <div id="msgbody">
                                            <div style={{marginLeft:"10px"}}>{msg.detail}</div>
                                        </div>
                                        <div id="msgclose" onClick={resetMsgs}>
                                            CLOSE
                                        </div>
                                    </div>
                                </div>
                            )

                        })

                        }
                    </>
                    : ""

            }
        </>
    )
}

