import TreeElement from "./TreeElement";
import '../css/treepage.css'
import { useEffect, useRef, useState } from "react";
import { createDataContainer, deleteDataContainer, delReference, getNodeInfo, setDataContainerValue, setNodeName, setNodeValue, setRefValue } from "../util/DataUtil";
import { Loadingarea } from "../util/Commoncomp"
import TargetPopup from "./TargetPopup";

const Treepage = ({ roots, workspace, setMsgs }) => {

    const [condobj, setCondobj] = useState({id:"",supertype:""});
    const [isLoading, setIsLoading] = useState("");

    const [result_id, setResultid] = useState("")
    const [result_name, setResultname] = useState("")
    const [result_lastedited, setResultLastEdited] = useState("")
    const [result_appstatus, setResultAppStatus] = useState("")
    const [result_values, setResultValues] = useState([])
    const [result_path, setResultpath] = useState([])
    const [result_dcs, setResultDcs] = useState([])
    const [result_refs, setResultRefs] = useState([])

    const [curretntab, setCurrentTab] = useState("GENE")
    const [refresh, setRefresh] = useState(false)

    const [showtree, setShowTree] = useState(true)



    useEffect(() => {
        if (condobj.id !== "" && condobj.id !== undefined) {
            handleSearchbutton();
        }
    }, [condobj, workspace, curretntab, refresh])



    const getCurretnCondid = () => {
        return condobj.id;
    }
    const handleSearchbutton = () => {
        setIsLoading(true)
        getNodeInfo(workspace, condobj.supertype, condobj.id, false, (data) => {
            if (data.data[condobj.supertype] !== null) {
                setResultid(data.data[condobj.supertype].id)
                setResultname(data.data[condobj.supertype].name)
                setResultLastEdited(data.data[condobj.supertype].currentRevisionLastEdited);
                setResultAppStatus(data.data[condobj.supertype].approvalStatus);
                setResultValues(data.data[condobj.supertype].values)
                setResultpath(data.data[condobj.supertype].path)
                setResultDcs(data.data[condobj.supertype].dataContainers)
                setResultRefs(data.data[condobj.supertype].referencesByReferenceType)
            } else {
                setResultid("")
                setResultname("")
                setResultAppStatus();
                setResultLastEdited();
                setResultValues([])
                setResultpath([])
                setResultDcs({})
                setResultRefs([])
            }

            setIsLoading(false)
        });
    }

    const handleMutErr = (result) =>{
        
        result.data.mut.errors.map(data => {
            setMsgs([{ type: "fail", detail: data.message }])
            setRefresh(!refresh);
        })
    }



    const changeTab = (tabid) => {
        setCurrentTab(tabid);
    }

    const GeneralTab = ({ result_id, result_name, result_values, result_lastedited, result_appstatus }) => {
        const [generaltab_name, setGeneralName] = useState(result_name)

        const onChangeResultName = (e) => {
            const value = e.target.value;
            setGeneralName(value);
        }

        const setResultName = (e) => {
            const value = e.target.value;
            setNodeName(workspace, condobj.supertype, result_id, value, () => {
                console.log('이름변경완료')
            })
        }




        const InputAtt = ({ index, value }) => {
            const [valueobj, setValueObj] = useState({ ...value });

            const onChangeResultValue = (e) => {
                const value = e.target.value;
                console.log(value)
                setValueObj({ ...valueobj, simpleValue: value })
            }



            const setResultValue = (e, attid) => {
                setNodeValue(workspace, condobj.supertype, result_id, attid, e.target.value, (result) => {
                    if (result.data.mut.success === true) {
                    }else{
                        handleMutErr(result);
                    }
                })
            }

            const setDateValue = (e, attid) => {
                let datevalue = e.target.value;
                datevalue = datevalue.replace("T", " ")
                setNodeValue(workspace, condobj.supertype, result_id, attid, datevalue, (result) => {
                    if (result.data.mut.success === true) {
                    }else{
                        handleMutErr(result);
                    }
                    
                })
            }

            if ((valueobj.simpleValue + "").indexOf('\n') >= 0) {
                return (
                    <textarea disabled={valueobj.attribute.calculated || (workspace === 'Approved') ? true : false}
                        id={index + "^" + valueobj.attribute.id} value={valueobj.simpleValue === null ? "" : valueobj.simpleValue}
                        onChange={(e) => { onChangeResultValue(e) }} onBlur={(e) => { setResultValue(e, valueobj.attribute.id) }}></textarea>
                )

            } else {
                if (valueobj.attribute.listOfValues !== null) {
                    return (
                        <div className="select_input">
                            <select className="select" id={index + "^" + valueobj.attribute.id} value={valueobj.simpleValue}
                                onChange={(e) => { onChangeResultValue(e); setResultValue(e, valueobj.attribute.id) }}
                                disabled={workspace === "Main" ? false : true}>
                                {valueobj.attribute.listOfValues.valueEntries.pageElements.map(valentry => {
                                    return (
                                        <option key={valueobj.attribute.id + valentry.value} value={valentry.value} selected={valueobj.simpleValue === valentry.value ? true : false}>
                                            {valentry.value}
                                        </option>
                                    )
                                })
                                }
                            </select>
                            <div></div>
                        </div>

                    )
                } else if (valueobj.attribute.validation !== null && valueobj.attribute.validation.name === 'isodatetime') {

                    return (
                        <input disabled={value.attribute.calculated || (workspace === 'Approved') ? true : false} type="datetime-local" value={valueobj.simpleValue}
                            onChange={e => { onChangeResultValue(e) }} onBlur={(e) => { setDateValue(e, valueobj.attribute.id) }} step="1"
                        ></input>
                    )
                } else {
                    return (
                        <input disabled={value.attribute.calculated || (workspace === 'Approved') ? true : false}
                            id={index + "^" + valueobj.attribute.id} value={valueobj.simpleValue === null ? "" : valueobj.simpleValue} onChange={(e) => { onChangeResultValue(e) }}
                            onBlur={(e) => { setResultValue(e, valueobj.attribute.id) }}></input>
                    )
                }
            }

        }



        return (
            <>
                {result_id !== "" ?
                    <div className="tablearea">
                        <table className='table' style={{ width: '100%' }}>
                            <thead>
                                <tr>
                                    <th colSpan={20}>
                                        General Info
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    result_id !== "" ?
                                        <>
                                            <tr>
                                                <td width='20%'><b>ID</b></td>
                                                <td>{result_id}</td>
                                            </tr>
                                            <tr>
                                                <td><b>NAME</b></td>
                                                <td>
                                                    <input disabled={workspace === 'Approved' ? true : false}
                                                        id="general_name" value={generaltab_name} onChange={(e) => { onChangeResultName(e) }}
                                                        onBlur={(e) => { setResultName(e) }}></input>
                                                </td>
                                            </tr>
                                            <tr>
                                                <td><b>LastEdited</b></td>
                                                <td>{result_lastedited}</td>
                                            </tr>
                                            <tr>
                                                <td><b>ApprovalStatus</b></td>
                                                <td>{result_appstatus}</td>
                                            </tr>
                                            {result_values.map(
                                                (value, index) => (
                                                    <tr key={value.attribute.id}>
                                                        <td><b>{value.attribute.title}</b></td>
                                                        <td>
                                                            <InputAtt key={result_id + "_" + value.attribute.id} index={index} value={value} />
                                                        </td>
                                                    </tr>
                                                )
                                            )
                                            }
                                        </>
                                        : <tr>
                                            <td>Not found Data.</td>
                                        </tr>
                                }
                            </tbody>
                        </table>
                    </div>

                    : ""
                }
            </>
        )
    }

    const DataContainerTab = ({ result_dcs }) => {
        const [dctab_dcs, setDcDcs] = useState([...result_dcs])

        const createDC = (index, dttype) => {
            createDataContainer(result_id, condobj.supertype, dttype, (result) => {
                if (result.data.mut.success === true) {
                    const newdt_id = result.data.mut.dataContainer.id;
                    const netdt_values = result.data.mut.dataContainer.values;
                    let temp_dcs = [...dctab_dcs]
                    temp_dcs[index].dataContainers.push({ id: newdt_id, values: netdt_values })
                    setDcDcs(temp_dcs)
                } else {
                    handleMutErr(result);
                }
            })
        }

        const delDC = (dc_id, i_index, j_index) => {
            deleteDataContainer(dc_id, (result) => {
                if (result.data.mut.success === true) {
                    let temp_dcs = [...dctab_dcs]
                    let temp = temp_dcs[i_index].dataContainers.filter((data, index) => {
                        if (Number(index) !== Number(j_index)) {
                            return true;
                        } else {
                            return false;
                        }
                    })
                    temp_dcs[i_index].dataContainers = [...temp]
                    setDcDcs(temp_dcs)
                } else {
                    handleMutErr(result);
                }
            })
        }

        const DataContainerForm = ({ dataContainers, formtype, i_index }) => {
            //formtype 1:헤더가 없는 form, 2: 헤더가 있는 form
            const [dcform_dcs, setDcformDcs] = useState([...dataContainers]);

            const InputAtt = ({ j_index, value_index, value }) => {
                const [valueobj, setValueObj] = useState({ ...value });

                const onChangeResultDC = (e) => {
                    const value = e.target.value;
                    setValueObj({ ...valueobj, simpleValue: value })
                }

                const setResultDCValue = (e, dcid, attid) => {
                    setDataContainerValue(workspace, dcid, attid, e.target.value, (result) => {
                        if (result.data.mut.success === true) {
                            let temp_formdcs = [...dcform_dcs]
                            temp_formdcs[j_index].values[value_index].simpleValue = e.target.value;
                            setDcformDcs(temp_formdcs)
                        } else {
                            handleMutErr(result);
                        }
                    })
                }

                const setDateValue = (e, dcid, attid) => {
                    let datevalue = e.target.value;
                    datevalue = datevalue.replace("T", " ")
                    setDataContainerValue(workspace, dcid, attid, datevalue, (result) => {
                        if (result.data.mut.success === true) {
                            let temp_formdcs = [...dcform_dcs]
                            temp_formdcs[j_index].values[value_index].simpleValue = e.target.value;
                            setDcformDcs(temp_formdcs)
                        } else {
                            handleMutErr(result);
                        }
                    })
                }

                if ((valueobj.simpleValue + "").indexOf('\n') >= 0) {
                    return (
                        <textarea disabled={valueobj.attribute.calculated || (workspace === 'Approved') ? true : false}
                            id={dcform_dcs[j_index].id + "^" + valueobj.attribute.id} value={valueobj.simpleValue}
                            onChange={(e) => { onChangeResultDC(e) }} onBlur={(e) => { setResultDCValue(e, dcform_dcs[j_index].id, valueobj.attribute.id) }}></textarea>
                    )

                } else {
                    if (valueobj.attribute.listOfValues !== null) {
                        const simipleValue = valueobj.simpleValue;
                        return (
                            <div className="select_input">
                                <select className="select" id={dcform_dcs[j_index].id + "^" + valueobj.attribute.id}
                                    onChange={(e) => { onChangeResultDC(e); setResultDCValue(e, dcform_dcs[j_index].id, valueobj.attribute.id) }}
                                    disabled={workspace === "Main" ? false : true}
                                >
                                    {dcform_dcs[j_index].values[value_index].attribute.listOfValues.valueEntries.pageElements.map(valentry => {
                                        return (
                                            <option value={valentry.value} selected={simipleValue === valentry.value ? true : false}>
                                                {valentry.value}
                                            </option>
                                        )
                                    })
                                    }
                                </select>
                                <div></div>
                            </div>

                        )
                    } else if (valueobj.attribute.validation !== null && valueobj.attribute.validation.name === 'isodatetime') {
                        return (
                            <input disabled={value.attribute.calculated || (workspace === 'Approved') ? true : false} type="datetime-local" value={valueobj.simpleValue} onChange={e => { onChangeResultDC(e) }}
                                onBlur={(e) => { setDateValue(e, dcform_dcs[j_index].id, valueobj.attribute.id) }} step="1"></input>
                        )

                    } else {
                        return (
                            <input disabled={valueobj.attribute.calculated || (workspace === 'Approved') ? true : false}
                                id={dcform_dcs[j_index].id + "^" + valueobj.attribute.id} value={valueobj.simpleValue}
                                onChange={(e) => { onChangeResultDC(e) }} onBlur={(e) => { setResultDCValue(e, dcform_dcs[j_index].id, valueobj.attribute.id) }}></input>
                        )
                    }
                }

            }


            if (formtype === 1) {
                return (
                    <>
                        {
                            dcform_dcs.length > 0 ?
                                <>
                                    {dcform_dcs.map((dcs, j_index) => {
                                        return (
                                            <>
                                                <tr>
                                                    <td width='20%'>ID</td>
                                                    <td>
                                                        {dcs.id}
                                                        {workspace === 'Main' ?
                                                            <div style={{ position: "absolute", top: "40%", width: "5%", right: "0.5%" }} onClick={() => { delDC(dcs.id, i_index, j_index) }} className="delbtn">
                                                                <div></div>
                                                                <div></div>
                                                            </div>
                                                            : ""}
                                                    </td>
                                                </tr>
                                                {dcs.values.map((value, value_index) => {
                                                    return (
                                                        <tr>
                                                            <td>
                                                                {value.attribute.id}
                                                            </td>
                                                            <td>
                                                                <InputAtt j_index={j_index} value_index={value_index} value={value} />
                                                            </td>

                                                        </tr>
                                                    )
                                                })}
                                            </>
                                        )
                                    })}
                                </>
                                :
                                <tr>
                                    <td>Not found data.</td>
                                </tr>
                        }
                    </>
                )
            } else {
                return (
                    <>
                        {
                            dcform_dcs.length > 0 ?
                                dcform_dcs.map((dcs, j_index) => {
                                    return (
                                        <>
                                            {j_index === 0 ?
                                                <tr style={{ textAlign: 'center' }}>
                                                    <td>ID</td>
                                                    {dcs.values.map(value => {
                                                        return (
                                                            <td>
                                                                {value.attribute.id}
                                                            </td>
                                                        )
                                                    })}
                                                    <td width={"5%"}></td>
                                                </tr>
                                                : ""
                                            }
                                            <tr>
                                                <td width='7%'>{dcs.id}</td>
                                                {
                                                    dcs.values.map((value, value_index) => {
                                                        return (
                                                            <td>
                                                                <InputAtt j_index={j_index} value_index={value_index} value={value} />
                                                            </td>
                                                        )

                                                    })
                                                }
                                                <td>
                                                    {workspace === 'Main' ?
                                                        <div className='delbtn' onClick={() => { delDC(dcs.id, i_index, j_index) }}>
                                                            <div></div>
                                                            <div></div>
                                                        </div>
                                                        : ""}
                                                </td>

                                            </tr>
                                        </>

                                    )

                                }) :
                                <tr>
                                    <td>Not found data.</td>
                                </tr>
                        }
                    </>
                )
            }
        }
        return (
            <div className="tablearea">
                {
                    dctab_dcs.length > 0 ?
                        dctab_dcs.map((dc, i_index) => {
                            return (

                                <table className='table' style={{ width: '100%' }}>
                                    <thead>
                                        <tr>
                                            <th colSpan={200}>
                                                {dc.dataContainerType.name}
                                                {workspace === 'Main' && (dc.dataContainerType.multivalued || (!dc.dataContainerType.multivalued && dc.dataContainers.length < 1)) ?
                                                    <div className='addbtn' onClick={(e) => { createDC(i_index, dc.dataContainerType.id); }}>
                                                        +
                                                    </div>
                                                    : ""}
                                            </th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {dc.dataContainerType.multivalued === false || dc.dataContainerType.multivalued === 'false' ?
                                            <DataContainerForm key={dc.dataContainerType.id} dataContainers={dc.dataContainers}
                                                formtype={1} i_index={i_index} />
                                            :
                                            <DataContainerForm key={dc.dataContainerType.id} dataContainers={dc.dataContainers}
                                                formtype={2} i_index={i_index} />
                                        }
                                    </tbody>
                                </table>
                            )
                        })
                        : ""
                }
            </div>
        );
    }




    const ReferenceTab = ({ result_refs }) => {
        const [reftab_refs, setRefTabRefs] = useState([...result_refs]);
        const [showTargetpopup, setShowTargetpopup] = useState(false);
        const validTargetObjtypes = useRef([])
        const refs_i_index = useRef(-1);
        const [refvalues, setRefValues] = useState([])
        const [reftypeid, setRefTypeId] = useState("")

        let map_refid_targettype = new Map();


        reftab_refs.map(ref => {
            map_refid_targettype.set(ref.referenceType.id, ref.referenceType.validTargetObjectTypes)
            return "";
        })



        const InputAtt = ({ value, targetid, reftypeid, i_index, j_index, value_index }) => {
            const [valueobj, setValueObj] = useState({ ...value });

            const onChangeValue = (e) => {
                console.log(e)
                setValueObj({ ...valueobj, simpleValue: e.target.value })
            }

            const setResultRefs = (e, targetid, reftypeid) => {
                setRefValue(result_id, condobj.supertype, targetid, reftypeid, e.target.id, e.target.value, (result) => {
                    if (result.data.mut.success === true) {
                        let temp_refs = [...reftab_refs]
                        temp_refs[i_index].referenceEntries[j_index].values[value_index].simpleValue = e.target.value;
                        setRefTabRefs(temp_refs);

                    } else {
                        handleMutErr(result);
                        handleSearchbutton();
                    }
                })

            }

            const setDateValue = (e, targetid, reftypeid) => {
                let datevalue = e.target.value;
                datevalue = datevalue.replace("T", " ");
                setRefValue(result_id, condobj.supertype, targetid, reftypeid, e.target.id, datevalue, (result) => {
                    if (result.data.mut.success === true) {
                        let temp_refs = [...reftab_refs]
                        temp_refs[i_index].referenceEntries[j_index].values[value_index].simpleValue = e.target.value;
                        setRefTabRefs(temp_refs);
                    } else {
                        handleMutErr(result);
                        handleSearchbutton();
                    }
                })
            }


            if ((valueobj.simpleValue + "").indexOf('\n') >= 0) {
                return (
                    <textarea disabled={valueobj.attribute.calculated || (workspace === 'Approved') ? true : false}
                        id={value.attribute.id} value={valueobj.simpleValue}
                        onChange={(e) => { onChangeValue(e) }} onBlur={(e) => { setResultRefs(e, targetid, reftypeid) }}></textarea>
                )

            } else {
                if (valueobj.attribute.listOfValues !== null) {
                    const simipleValue = valueobj.simpleValue;
                    return (
                        <div className="select_input">
                            <select className="select" id={value.attribute.id} onChange={(e) => { onChangeValue(e); setResultRefs(e, targetid, reftypeid) }}
                                disabled={workspace === "Main" ? false : true} >
                                {valueobj.attribute.listOfValues.valueEntries.pageElements.map(valentry => {
                                    return (
                                        <option value={valentry.value} selected={simipleValue === valentry.value ? true : false}>
                                            {valentry.value}
                                        </option>
                                    )
                                })
                                }
                            </select>
                            <div></div>
                        </div>

                    )
                } else if (valueobj.attribute.validation !== null && valueobj.attribute.validation.name === 'isodatetime') {
                    return (
                        <input disabled={value.attribute.calculated || (workspace === 'Approved') ? true : false} type="datetime-local" value={valueobj.simpleValue}
                            onChange={e => { onChangeValue(e) }} onBlur={(e) => { setDateValue(e, targetid, reftypeid) }} step="1" ></input>
                    )

                } else {
                    return (
                        <input disabled={value.attribute.calculated || (workspace === 'Approved') ? true : false} id={value.attribute.id} value={valueobj.simpleValue}
                            onChange={(e) => { onChangeValue(e) }} onBlur={(e) => { setResultRefs(e, targetid, reftypeid) }}></input>
                    )
                }
            }

        }

        const showTargetPopup = (reftype, index, values) => {
            refs_i_index.current = index;
            setRefValues(values)
            setRefTypeId(reftype)
            validTargetObjtypes.current = map_refid_targettype.get(reftype);
            setShowTargetpopup(true)
        }

        const delRef = (targetid, reftypeid, i_index, j_index) => {
            delReference(result_id, condobj.supertype, targetid, reftypeid, (result) => {

                if (result.data.mut.success === true) {
                    let temp_refs = [...reftab_refs]
                    temp_refs[i_index].referenceEntries = reftab_refs[i_index].referenceEntries.filter((data, index) => {
                        return (Number(index) !== Number(j_index));
                    })
                    setRefTabRefs(temp_refs)
                } else {
                    handleMutErr(result);
                    handleSearchbutton()
                }
            })
        }
        return (
            <div className="tablearea">
                {reftab_refs.map((ref, i_index) => {
                    return (
                        <>
                            <TargetPopup key={"TargetPopup_" + i_index} setRefTabRefs={setRefTabRefs} reftab_refs={reftab_refs}
                                workspace={workspace} supertype={condobj.supertype} validTargetObjtypes={validTargetObjtypes}
                                refs_i_index={refs_i_index} setShowTargetpopup={setShowTargetpopup} showTargetpopup={showTargetpopup}
                                refvalues={refvalues} reftypeid={reftypeid} result_id={result_id}
                            />

                            <table key={"Reftable_" + i_index} className="table" style={{ width: '100%' }}>
                                <thead>
                                    <tr>
                                        <th colSpan={200}>{ref.referenceType.id}
                                            {workspace === 'Main' && (ref.referenceType.multivalued || (!ref.referenceType.multivalued && ref.referenceEntries.length < 1)) ?
                                                <div className='addbtn' onClick={(e) => { showTargetPopup(ref.referenceType.id, i_index, ref.referenceType.values) }}>
                                                    +
                                                </div>
                                                : ""}
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {ref.referenceEntries.length > 0 ?
                                        <>
                                            {ref.referenceEntries.map((entry, j_index) => {
                                                return (
                                                    <>
                                                        {j_index === 0 ?
                                                            <tr style={{ textAlign: 'center' }}>
                                                                <td>Target ID</td>
                                                                <td>Target NAME</td>
                                                                {ref.referenceType.validAttributes.map(validatt => {
                                                                    return (
                                                                        <td>{validatt.id}</td>
                                                                    )
                                                                })}
                                                                <td width={"5%"}></td>
                                                            </tr>
                                                            : ""}
                                                        <tr>
                                                            <td>
                                                                {entry.target.id}
                                                            </td>
                                                            <td>
                                                                {entry.target.name}
                                                            </td>
                                                            {ref.referenceType.validAttributes.map(validatt => {
                                                                let temp_value_index = -1;
                                                                const temp_value = entry.values.filter((value, index) => {
                                                                    if (value.attribute.id === validatt.id) {
                                                                        temp_value_index = index;
                                                                        return true;
                                                                    } else {
                                                                        return false;
                                                                    }
                                                                })
                                                                return (
                                                                    <td>
                                                                        <InputAtt key={entry.target.id + "_" + temp_value[0].attribute.id} value={temp_value[0]}
                                                                            targetid={entry.target.id} reftypeid={ref.referenceType.id} i_index={i_index} j_index={j_index} value_index={temp_value_index} />
                                                                    </td>
                                                                )
                                                            })}
                                                            <td>
                                                                {workspace === 'Main' ?
                                                                    <div className='delbtn' onClick={(e) => { delRef(entry.target.id, ref.referenceType.id, i_index, j_index) }}>
                                                                        <div></div>
                                                                        <div></div>
                                                                    </div>
                                                                    : ""}
                                                            </td>
                                                        </tr>
                                                    </>
                                                )
                                            })}
                                        </>
                                        :
                                        <tr>
                                            <td>Not found data.</td>
                                        </tr>
                                    }


                                </tbody>
                            </table>
                        </>


                    )
                })

                }
            </div>
        )
    }

    return (
        <>
            <Loadingarea isLoading={isLoading} />
            <div className="treemain">

                <div className={!showtree ? "treearea treeareahide" : "treearea"} >
                    <table>
                        <thead>
                            {/* <tr>
                                <th colSpan={10} style={{ textAlign: 'left' }}>
                                    <h2 style={{ textTransform: 'uppercase' }}>{supertype} hierarchy</h2>
                                </th>
                            </tr> */}
                        </thead>
                        <tbody>
                            {roots.map(root=>{
                                return (
                                    <TreeElement key={root.id} supertype={root.supertype} id={root.id} name={root.name} lvl="0" setCondobj={setCondobj}
                                    getCurretnCondid={getCurretnCondid} setIsLoading={setIsLoading} workspace={workspace}
                                    result_path={result_path} setMsgs={setMsgs}/>
                                )
                            })

                            }
                            
                        </tbody>
                    </table>
                </div>

                <div className="nodedetail">
                    <div className='searcondition_area' >
                        <div className="showtree_area">
                            <input id="chk_showtree" type="checkbox" value={showtree} onChange={() => { setShowTree(!showtree) }}></input>
                            <label htmlFor="chk_showtree">
                                <div></div>
                            </label>
                        </div>
                        {result_path.map((path) => {
                            let postfix = "->";
                            return (
                                <>{path.id + postfix}</>
                            )
                        })}<div style={{ color: "#ff4e00" }}>{result_id}</div>
                        <div className="refresh-icon" onClick={handleSearchbutton} ></div>
                    </div>
                    <div className="tabmenu">
                        <div className={curretntab === 'GENE' ? "tab-selected" : ""} onClick={() => { changeTab('GENE') }}>General</div>
                        <div className={curretntab === 'DC' ? "tab-selected" : ""} onClick={() => { changeTab('DC') }}>Data Container</div>
                        <div className={curretntab === 'REF' ? "tab-selected" : ""} onClick={() => { changeTab('REF') }}>Reference</div>
                    </div>
                    {curretntab === 'GENE' ?
                        <GeneralTab key="GeneralTab" result_id={result_id} result_name={result_name} result_values={result_values}
                            result_lastedited={result_lastedited} result_appstatus={result_appstatus} />
                        : ""
                    }
                    {curretntab === 'DC' ?
                        <DataContainerTab key="DataContainerTab" result_dcs={result_dcs} />
                        : ""
                    }
                    {curretntab === 'REF' ?
                        <ReferenceTab key="ReferenceTab" result_refs={result_refs} />
                        : ""
                    }

                </div>

            </div >
        </>


    )
}

export default Treepage;