import axios from "axios";
import qs from 'qs'
import cookies from "react-cookies";


let supertype_map = new Map()
supertype_map.set('product', 'Product')
supertype_map.set('entity', 'Entity')
supertype_map.set('asset', 'Asset')
supertype_map.set('classification', 'Classification')


const childrenquery = '    children{ ' +
    '      pageElements{ ' +
    '        id ' +
    '        name ' +
    '        objectType{ ' +
    '           id  ' +
    '           title ' +
    '        } ' +
    '      } ' +
    '    } ';
const lovquery = ' listOfValues { ' +
    '   id ' +
    '   name ' +
    '   valueEntries{ ' +
    '       pageElements{ ' +
    '           valueId ' +
    '           value ' +
    '       } ' +
    '   } ' +
    ' }';

const valuesquery = '    values{ ' +
    '      __typename ' +
    '      attribute{ ' +
    '        title ' +
    '        id ' +
    '        calculated ' +
    '        validation{name} ' +
    lovquery +
    '      } ' +
    '      simpleValue ' +
    '    }     ';
const referencesquery = '    referencesByReferenceType{ ' +
    '      referenceType{ ' +
    '        id ' +
    '        multivalued ' +
    '        validAttributes {id} ' +
    '        validTargetObjectTypes{ ' +
    '           id ' +
    '        } ' +
    '      } ' +
    '      referenceEntries{ ' +
    valuesquery +
    '        target{ ' +
    '          ... on Entity{ ' +
    '            id ' +
    '            name ' +
    '            objectType{id} ' +
    '          } ' +
    '         ... on Product{ ' +
    '            id ' +
    '          } ' +
    '        } ' +
    '      } ' +
    '    } ';
const dataconatinerquery = '    dataContainers{ ' +
    '      dataContainerType{ ' +
    '        id ' +
    '        name ' +
    '        validAttributes{id} ' +
    '        multivalued ' +
    '      } ' +
    '      dataContainers{ ' +
    '        id ' +
    valuesquery +
    '      } ' +
    '    } ';


export const doLogin = (userid, password, callbackmethod) => {
    refreshToken(userid, password, (result) => {
        callbackmethod(result);
        if (result.success === 'N') {
        } else {
            window.location.href = '/'
        }


    })

}

const refreshToken = (userid, password, callbackmethod) => {
    if (userid === undefined) {
        localStorage.clear();
        callbackmethod({ success: 'N', msg: "세션만료" });
        return "";
    }

    const inputparms = {
        userId: userid,
        password: password
    }
    axios.post("https://" + localStorage.getItem('stepurl') + "/graphqlv2/auth", qs.stringify(inputparms), {
        headers: {
            "Content-Type": `application/x-www-form-urlencoded`,
            // "Origin": localStorage.getItem('stepurl'),
            // 'Access-Control-Request-Headers':'Authorization, Content-Type'
        }
    }).then(res => {
        console.log(res)
        if (res.status === 200) {
            localStorage.setItem("token", res.data);
            let expires = new Date()
            expires.setTime(new Date().getTime() + 1000000);
            cookies.save('userid', userid, { path: '/', expires: expires })
            cookies.save('password', password, { path: '/', expires: expires })
        }
        callbackmethod({ success: 'Y', msg: "" });
    }).catch(err => {
        localStorage.clear();
        callbackmethod({ success: 'N', msg: err });
    });
}

export const searchForNode = (workspace, id, objtypes, callbackmethod) => {
    let query;

    let condobjtypes = []

    objtypes.map(obj => {
        condobjtypes.push({ objectType: { objectType: obj.id } })
    })

    let typecondition = "";
    if (objtypes.length > 1) {
        typecondition = '  { or:{conditions:' + JSON.stringify(condobjtypes) + '  } } ';
    } else {
        typecondition = '  {objectType:{objectType:"#TYPEID"}}  ';
        typecondition = typecondition.replace('#TYPEID', objtypes[0].id)
    }


    query = '{ ' +
        'a:  searchForEntities(context: "Context1", workspace: "#WORKSPACE",  ' +
        '    condition: { ' +
        '      					and: { ' +
        '                  			conditions: [ ' +
        '                          	{or:{conditions:[{id: {queryString: "#ID*", operator: like}},{name: {queryString: "#NAME*", operator: like}}]}}, ' +
        typecondition +
        '                        ] ' +
        '                		} ' +
        '                     ' +
        '              } ' +
        '  ) { ' +
        '    pageElements { ' +
        '      id ' +
        '      name ' +
        '      objectType { ' +
        '        id ' +
        '      } ' +
        '    } ' +
        '  } ' +
        'b:  searchForProducts(context: "Context1", workspace: "#WORKSPACE",  ' +
        '    condition: { ' +
        '      					and: { ' +
        '                  			conditions: [ ' +
        '                          	{or:{conditions:[{id: {queryString: "#ID*", operator: like}},{name: {queryString: "#NAME*", operator: like}}]}}, ' +
        typecondition +
        '                        ] ' +
        '                		} ' +
        '                     ' +
        '              } ' +
        '  ) { ' +
        '    pageElements { ' +
        '      id ' +
        '      name ' +
        '      objectType { ' +
        '        id ' +
        '      } ' +
        '    } ' +
        '  } ' +
        '} ';
    query = query.replace(/#WORKSPACE/gi, workspace).replace(/#ID/gi, id).replace(/#NAME/gi, id);
    console.log(query)

    const jsonobj = { query: query };

    dofetch(jsonobj, callbackmethod)
}

export const getNodeInfo = (workspace, supertype, id, isGetChildren, callbackmethod) => {


    const getNodeInfoquery = 'query { ' +
        '  #SUPERTYPE(id:"#ID" context:"Context1" workspace:"#WORKSPACE"){ ' +
        '    id ' +
        '    name ' +
        '   currentRevisionLastEdited ' +
        '   approvalStatus ' +
        '    path { ' +
        '      id ' +
        '    } ' +
        '    objectType{ ' +
        '      id  ' +
        '      title ' +
        '    } ' +
        valuesquery +
        "#CHILDREN" +
        referencesquery +
        dataconatinerquery +
        '  } ' +
        '} ';
    let query = getNodeInfoquery;
    if (isGetChildren) {
        query = query.replace('#CHILDREN', childrenquery)
    } else {
        query = query.replace('#CHILDREN', "")
    }

    query = query.replace('#SUPERTYPE', supertype).replace('#ID', id).replace('#WORKSPACE', workspace);
    const jsonobj = { query: query };

    dofetch(jsonobj, callbackmethod)
}

export const setNodeValue = (workspace, supertype, id, attid, value, callbackmethod) => {
    let query = 'mutation { ' +
                'mut:  setNodeValue(context: "Context1", workspace: "#WORKSPACE", input: {id: "#ID", type: #SUPERTYPE, attribute: "#ATTID", value: {simpleValue: "#VALUE"}}) { ' +
                '    success ' +
                '    errors { ' +
                '      message ' +
                '    } ' +
                '  } ' +
                '} ';
    query = query.replace('#ID', id).replace('#SUPERTYPE', supertype_map.get(supertype)).replace('#ATTID', attid).replace('#VALUE', value).replace('#WORKSPACE', workspace);
    const jsonobj = { query: query };

    dofetch(jsonobj, callbackmethod)

}

