const dofetch = (jsonobj, callbackmethod) => {

    refreshToken(cookies.load('userid'), cookies.load('password'), (result) => {
        if (result.success === 'N') {
            window.location.href = '/?sessionvalid=N'
            return "";
        }

        const authkey = 'Bearer ' + localStorage.getItem('token');
        fetch('https://' + localStorage.getItem('stepurl') + '/graphqlv2/graphql', {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                "Authorization": authkey,
                "Origin": localStorage.getItem('stepurl'),
                'Access-Control-Request-Headers': 'Authorization, Content-Type'
            },
            body: JSON.stringify(jsonobj),
        }).then(res => {
            console.log(res.status)
            if (res.status === 200) {
                console.log('success')
            } else {
                console.log('error')
            }
            return res.json()
        }).then(data => {
            console.log(data)
            callbackmethod(data)

        })
    })

}