import React, { useState } from 'react'
import Product from './Product'
import Entity from './Entity'
import { Link, Route, Routes } from 'react-router-dom'
import Treepage from './Treepage'
import cookies from "react-cookies";
import { Msgarea } from '../util/Commoncomp'


export default function Header() {

    const [currentmenu, setCurrentMenu] = useState('producttree');
    const [workspace, setWorkspace] = useState('Main')
    const [msgs, setMsgs] = useState([])
    //const [roots1,setRoots1] = useState([{supertype:"product",id:"Product hierarchy root", name:"Product hierarchy root" }])
    const [roots, setRoots] = useState([{ supertype: "product", id: "Product hierarchy root", name: "Product hierarchy root" },
    { supertype: "entity", id: "100305", name: "Entity root" },
    { supertype: "entity", id: "DUHK_TEST", name: "DUHK_TEST" }

    ])


    function handleLogout() {
        cookies.remove('userid')
        window.localStorage.clear();
        window.location.href = "/";
    }

    const changeWorkspace = () => {
        if (workspace === 'Main') {
            setWorkspace('Approved')
        } else {
            setWorkspace('Main')
        }
    }

    const resetMsgs = () => {
        setMsgs([])
    }



    return (

        <div style={{ width: '100%', height: "100%", display: 'flex', backgroundColor: "white", justifyContent: "center" }}>
            <div style={{ width: '95%', display: 'block' }}>
                <Msgarea key="Msgarea" msgs={msgs} resetMsgs={resetMsgs} />
                <div className='header'>

                    <h1 style={{ width: '30%', display: 'flex', justifyContent: 'start', alignItems: 'end', position: "relative" }}><div className='step-logo' />STEP GraphQL UI</h1>
                    <div className='step-url' >{localStorage.getItem('stepurl')}</div>
                    <div style={{ display: 'flex', alignItems: 'center', width: '22%', justifyContent: 'right' }}>
                        <button className='btn-default' onClick={handleLogout}>Logout</button>
                    </div>
                </div>
                <div style={{ display: 'flex', justifyContent: 'right', marginTop: '5px', marginBottom: "10px" }}>
                    <div className='select_ws' id={workspace} onClick={changeWorkspace}>
                        <div>Main</div>
                        <div>Approved</div>
                    </div>
                </div>

                {/* <div className='headermenu'>
                    <Link to="/" className={currentmenu === 'producttree' ? "menubutton menubutton-selected" : "menubutton"} onClick={() => { setCurrentMenu('producttree') }}>Product Tree</Link>
                    <Link to="/entitytree" className={currentmenu === 'entitytree' ? "menubutton menubutton-selected" : "menubutton"} onClick={() => { setCurrentMenu('entitytree') }}>Entity Tree</Link>

                </div> */}

                <div className='mainarea'>
                    <Routes>
                        <Route path="/" element={<Treepage key="tree_page" roots={roots} workspace={workspace} setMsgs={setMsgs} />} />
                        <Route path="/product" element={<Product />} />
                        <Route path="/entity" element={<Entity />} />
                    </Routes>
                </div>

            </div>
        </div>


    )

}
