import { useState } from "react";
import { createReference, getNodeInfo, searchForNode } from "../util/DataUtil";


const TargetPopup = ({ setRefTabRefs, reftab_refs, workspace, supertype, validTargetObjtypes, refs_i_index, setShowTargetpopup, showTargetpopup, refvalues,reftypeid, result_id }) => {
    //Browse Tab
    const [curObj, setCurObj] = useState({ id: "", name: "" })
    const [msg, setMsg] = useState("")


    //Search Tab
    const [targetList, setTargetList] = useState([])
    const [targetid, setTargetid] = useState("")


    const [curTab, setCurTab] = useState(1)




    const init = () => {
        setCurObj({ id: "", name: "" })
        setMsg("")
        setTargetList([])
        setTargetid("")
        setCurTab(1)
    }


    const getCurId = () => {
        return curObj.id;
    }

    const onChangeTargetid = (e) => {

        const tmptargetid = e.target.value;
        setTargetid(tmptargetid)

        let temparr = [];
        searchForNode(workspace, tmptargetid, validTargetObjtypes.current, (data) => {
            data.data.a.pageElements.map(data => {
                temparr.push(data)
            })
            data.data.b.pageElements.map(data => {
                temparr.push(data)
            })
            setTargetList(temparr)
        })


    }

    const selectTarget = (targetid, targetname) => {
        if (targetid !== undefined && targetid !== "") {
            createReference(result_id,supertype,targetid,reftypeid,(result)=>{
                if(result.data.createReference.success === true){
                    let temp_refs = [...reftab_refs];
                    let temp_values = [...result.data.createReference.reference.values];                    
                    temp_refs[refs_i_index.current].referenceEntries.push({ target: { id: targetid, name: targetname }, values: temp_values })
                    setRefTabRefs(temp_refs);
                                       
                }else{
                    result.data.createReference.errors.map(data =>{
                        alert("Fail-" + data.message);
                    })                    
                }
                init(); 
                setShowTargetpopup(false)
                
            })
            
        }
    }

    const changeTab = (tabid) => {
        setCurTab(tabid)
    }


    return (
        <>
            {showTargetpopup ?
                <div className="popup">
                    <div style={{ display: 'block', width: '50%', height: "800px", backgroundColor: '#e9e4d9', paddingLeft: '5px', paddingRight: '5px' }}>
                        <div style={{ display: 'flex', width: '100%', zIndex: '31', color: 'white', position: 'relative' }}>
                            <div className="tabmenu" style={{ width: '100%' }}>
                                <div onClick={() => { changeTab(1) }} className={curTab === 1 ? "tab-selected" : ""}>Browse</div>
                                <div onClick={() => { changeTab(2) }} className={curTab === 2 ? "tab-selected" : ""}>Search</div>
                            </div>
                            <div style={{ position: 'absolute', top: '4px', right: '1%' }}>
                                <button className="btn-small-close" onClick={() => { setShowTargetpopup(false); init(); }}>X</button>
                            </div>
                        </div>
                        {curTab === 1 ?
                            <>
                                <div style={{ overflow: 'scroll', height: '700px' }}>
                                    <table>
                                        <tbody>
                                            <PopupTreeElement key={"100305"} supertype={"entity"} id={"100305"} name={"Entity root"} objtypeid="Entity" lvl="0" setCurObj={setCurObj}
                                                getCurId={getCurId} validTargetObjtypes={validTargetObjtypes} setMsg={setMsg} />
                                            <PopupTreeElement key={"Product hierarchy root"} supertype={"product"} id={"Product hierarchy root"} name={"Product hierarchy root"} objtypeid="Product" lvl="0" setCurObj={setCurObj}
                                                getCurId={getCurId} validTargetObjtypes={validTargetObjtypes} setMsg={setMsg} />
                                        </tbody>

                                    </table>

                                </div>
                                <div style={{ display: 'flex', justifyContent: 'right', position: "relative" , marginTop:'10px'}}>
                                    <div style={{ position: "absolute", left: '10px', color: "red", fontWeight: "bold" }}>{msg}</div>
                                    <button className="btn-small" onClick={() => { if (msg === "") { selectTarget(curObj.id, curObj.name) } }}>Select</button>
                                </div>
                            </>

                            : ""
                        }

                        {curTab === 2 ?
                            <div style={{ display: 'block', width: '100%', overflow: 'scroll', height: '700px', alignItems: 'right', marginTop: '5px', marginBottom: '5px' }}>
                                <div style={{ display: 'flex' }}>
                                    <input type='text' value={targetid} onChange={(e) => { onChangeTargetid(e) }} style={{ width: '70%' }}></input>
                                    <button className="btn-small" style={{ marginLeft: '10px' }} onClick={(e) => { onChangeTargetid(e) }}>Search</button>
                                </div>

                                <table className="table" style={{ width: '100%' }}>
                                    <thead>
                                        <tr>
                                            <th>
                                                ID
                                            </th>
                                            <th>
                                                name
                                            </th>
                                            <th>
                                                Object Type
                                            </th>
                                        </tr>

                                    </thead>
                                    <tbody>
                                        {targetList.length > 0 ?
                                            <>
                                                {targetList.map(target => {
                                                    return (
                                                        <tr>
                                                            <td>
                                                                <div style={{ cursor: 'pointer', color: '#8442b7', fontWeight: 'bold' }} onClick={() => { selectTarget(target.id, target.name) }}>
                                                                    {target.id}
                                                                </div>

                                                            </td>
                                                            <td width='40%'>{target.name}</td>
                                                            <td>{target.objectType.id}</td>
                                                        </tr>
                                                    )
                                                })}
                                            </>
                                            :
                                            <tr>
                                                <td colSpan={20}>
                                                    Not found Data.
                                                </td>
                                            </tr>
                                        }
                                    </tbody>
                                </table>
                            </div>
                            : ""}

                    </div>

                </div>
                : ""
            }
        </>
    )
}


const PopupTreeElement = ({ supertype, id, name, objtypeid, lvl, setCurObj, getCurId, validTargetObjtypes, setMsg }) => {
    const [chdidrenData, setChildrenData] = useState([])
    const [showChild, setShowChild] = useState('N');

    // useEffect(() => {
    //     getNodeInfo("Main", supertype, id, (data) => {
    //         if (data.data[supertype] !== undefined) {
    //             setChildrenData(data.data[supertype].children.pageElements)
    //         }
    //     });
    // }, [showChild])




    const searchChildren = () => {
        console.log(showChild)
        if (showChild === 'Y') {
            setShowChild('N')
        } else {
            setShowChild('Y')
            getNodeInfo("Main", supertype, id,true, (data) => {
                if (data.data[supertype] !== undefined) {
                    setChildrenData(data.data[supertype].children.pageElements)
                }
            });
        }

    }

    const selectTarget = (id, name, objtypeid) => {
        setCurObj({ id: id, name: name })
        console.log(validTargetObjtypes.current)
        console.log(objtypeid)
        let isValid = false;
        validTargetObjtypes.current.map(data => {
            if (data.id === objtypeid) {
                isValid = true;
            }
        })

        if (!isValid) {
            setMsg("Not Valid Object Type.")
        } else {
            setMsg("")
        }

    }

    const marginleft = (Number(lvl) * 50) + "px";
    return (

        <>
            {id !== undefined ?
                <>
                    <tr style={{ textAlign: 'start' }}>
                        <td>
                            <div className="treeele" style={{ marginLeft: marginleft }}>
                                <b style={{ cursor: 'pointer' }} onClick={searchChildren} >{showChild === 'Y' ? " - " : " + "}</b>
                                <button id={id} className="btn-small" onClick={() => { selectTarget(id, name, objtypeid) }}>{id} - {name}</button>
                                {getCurId() === id ?
                                    <button id={id} className="btn-selected"></button>
                                    : ""
                                }
                            </div>

                        </td>
                    </tr>
                    {showChild === 'Y' ? chdidrenData.map(child => (<PopupTreeElement key={child.id} supertype={supertype} id={child.id} name={child.name}
                        objtypeid={child.objectType.id} lvl={Number(lvl) + 1} setCurObj={setCurObj} getCurId={getCurId} validTargetObjtypes={validTargetObjtypes} setMsg={setMsg} />)) : ""}
                </>
                :
                <tr>
                    <td>
                        Not Found Data.
                    </td>
                </tr>
            }
        </>

    );
}




export default TargetPopup;