import React, { useEffect, useState } from "react";
import { doApprove, getNodeInfo } from "../util/DataUtil";

const TreeElement = ({ supertype, id, name, lvl, setCondobj, getCurretnCondid, setIsLoading, workspace, setMsgs }) => {
    const [chdidrenData, setChildrenData] = useState([])
    const [showChild, setShowChild] = useState('N');
    const [showPopupMenu, setShowPopupMenu] = useState(false)
    const localId_showchild = workspace + "_" + id + "_showchild";
    const localId_data = workspace + "_" + id;

    const localId_Main_showchild = "Main_" + id + "_showchild";
    const localId_Approved_showchild = "Approved_" + id + "_showchild";

    useEffect(() => {
        const temp_data = localStorage.getItem(localId_data) !== "" ? JSON.parse(localStorage.getItem(localId_data)) : null;


        if (temp_data !== null && temp_data[supertype] !== undefined &&
            localStorage.getItem(localId_showchild) === 'Y') {
            setShowChild('Y')
            setChildrenData(temp_data[supertype].children.pageElements)
        } else {

            if (localStorage.getItem(localId_showchild) === 'Y') {
                //setIsLoading(true)
                getNodeInfo(workspace, supertype, id, true, (data) => {
                    localStorage.setItem(localId_data, JSON.stringify(data.data))
                    if (data.data[supertype] !== undefined) {
                        setChildrenData(data.data[supertype].children.pageElements)
                    }
                    //setIsLoading(false)
                });
            } else {
                setChildrenData([])
            }

        }
    }, [showChild, workspace])

    useEffect(() => {
        if (localStorage.getItem(localId_Main_showchild) === 'Y' || localStorage.getItem(localId_Approved_showchild) === 'Y') {
            setIsLoading(true);
            setShowChild('Y');
            getNodeInfo(workspace, supertype, id, true, (data) => {
                localStorage.setItem(localId_data, JSON.stringify(data.data))
                if (data.data[supertype] !== undefined && data.data[supertype] !== null) {
                    setChildrenData(data.data[supertype].children.pageElements)
                } else {
                    setChildrenData([])
                }
                setIsLoading(false)
            });
        }

    }, [workspace])
    useEffect(() => {
        setShowPopupMenu(false);
    }, [getCurretnCondid()])


    const searchChildren = () => {
        console.log(showChild)
        if (showChild === 'Y') {
            localStorage.setItem(localId_showchild, 'N')
            localStorage.removeItem(localId_data)
            setShowChild('N')
        } else {
            localStorage.setItem(localId_showchild, 'Y')
            setShowChild('Y')
        }

    }

    const showDetail = (e) => {
        setCondobj({ id: e.target.id, supertype: supertype })
    }

    const marginleft = (Number(lvl) * 30) + "px";
    return (

        <>
            {id !== undefined ?
                <>
                    <tr style={{ textAlign: 'start' }}>
                        <td>
                            <div className="treeele" style={{ marginLeft: marginleft }}>
                                <input type="checkbox" id={id + "_checkbox"} checked={showPopupMenu} onChange={e => { setShowPopupMenu(e.target.checked) }} />
                                <div className={showChild === "Y" ? "tree_prefix_showchild" : "tree_prefix"} onClick={searchChildren} ></div>
                                <div style={{ marginTop: "0px", display: "flex", position: "relative" }}>
                                    {getCurretnCondid() === id ?
                                        <>

                                            <label htmlFor={id + "_checkbox"}>
                                                <div></div>
                                            </label>
                                            <div className="popup_menu">
                                                <button className="btn-popup" onClick={() => {
                                                    doApprove(id, supertype, (result) => {
                                                        if (result.data.mut.success) {
                                                            setMsgs([{ type: "success", detail: "Approved complete" }])
                                                            setCondobj({ id: id, supertype: supertype })
                                                        } else {
                                                            let temp_msgs = []
                                                            result.data.mut.errors.map(error => {
                                                                temp_msgs.push({ type: "error", detail: error.message })
                                                            })
                                                            setMsgs(temp_msgs)
                                                        }
                                                    })
                                                }} >Approve</button>
                                                <button className="btn-popup" onClick={() => { alert("1") }}>Workflow</button>
                                                <button className="btn-popup" onClick={() => { alert("1") }}>Reject</button>
                                            </div>


                                        </>

                                        : ""
                                    }
                                    <button id={id} onClick={showDetail} style={{ fontWeight: getCurretnCondid() === id ? "bold" : "", color: getCurretnCondid() === id ? "#ff4e00" : "" }}>{id} - {name}</button>

                                </div>

                            </div>

                        </td>
                    </tr>
                    {showChild ? chdidrenData.map(child => (<TreeElement key={child.id} supertype={supertype} id={child.id} name={child.name}
                        lvl={Number(lvl) + 1} setCondobj={setCondobj} getCurretnCondid={getCurretnCondid} setIsLoading={setIsLoading}
                        workspace={workspace} setMsgs={setMsgs} />)) : ""}
                </>
                :
                <tr>
                    <td>
                        Not Found Data.
                    </td>
                </tr>
            }
        </>

    );

}

export default TreeElement;